import API from "../../../services/API";
import AD2 from "../../../assets/img/ad2.png";
import AD3 from "../../../assets/img/ad3.png";
import Carousel from "react-material-ui-carousel";
import { useDispatch } from "react-redux";
import { setRoute } from "../../../redux/actions/route";
import {
  clearCategoryFilter,
  clearBrandFilter,
} from "../../../redux/actions/viewFilter";

import { LazyLoadImage } from "react-lazy-load-image-component";
import "react-lazy-load-image-component/src/effects/blur.css";
import { useEffect, useState } from "react";

export const Ad = () => {
  const dispatch = useDispatch();
  const route = (pagename) => {
    dispatch(setRoute(pagename));
    dispatch(clearCategoryFilter());
    dispatch(clearBrandFilter());
  };

  const [images, setImages] = useState([]);

  useEffect(() => {
    // Load the images from the server's public directory
    const checkImage = async () => {
      const result = await fetch("http://51.15.21.77/file/getadvertcount");
      if (result.ok) {
        const resultText = await result.text();
        const resultJson = JSON.parse(resultText);
        let imgList = [];
        await resultJson.adverts.map((r, i) => {
          if (r) {
            imgList = [...imgList, `${API}/uploads/Advertisement${i + 1}.jpg`];
          }
          return r;
        });
        setImages(imgList);
      }
    };

    checkImage();
  }, []);
  return (
    <div>
      <Carousel indicators={false}>
        {images.length > 0 &&
          images.map((image, index) => (
            <LazyLoadImage
              key={index}
              src={image}
              alt={`ad${index + 1}`}
              className="w-full rounded-lg"
              effect="blur"
            />
          ))}
      </Carousel>
      <div className="flex mt-[1vh] justify-between">
        <div>
          <LazyLoadImage
            src={AD2}
            alt="ad2"
            className="w-[45vw] rounded-lg"
            onClick={() => route("all")}
          ></LazyLoadImage>
        </div>
        <div>
          <LazyLoadImage
            src={AD3}
            alt="ad3"
            className="w-[45vw] rounded-lg"
          ></LazyLoadImage>
        </div>
      </div>
    </div>
  );
};
export default Ad;
